import React, { Component } from 'react'

const defaultRule = [
  {
    id: 1,
    txt:
      '<p><em class="rule-heightlight">3</em></p>'
  },
  {
    id: 2,
    txt:
      '<p>500:005</p>'
  },
  {
    id: 3,
    txt:
      '<p></p>'
  },
  {
    id: 4,
    txt:
      '<p class="rule-heightlight"></p><p class="rule-heightlight">Call 11<br >2018iPhone++;</p><p class="rule-heightlight">Call 22/3<br >iPad++;</p><p class="rule-heightlight">Call 34/5/6<br >YSL++;</p><p class="rule-heightlight">Call 47-10<br >+</p>'
  },
  {
    id: 5,
    txt:
      '<p><em class="rule-heightlight">920</em></p>'
  },
  {
    id: 6,
    txt:
      '<p><em class="rule-heightlight">92118:00</em></p>'
  },
  {
    id: 7,
    txt:
      '<p></p>'
  },
  {
    id: 8,
    txt: '<p></p>'
  }
]

class DialogRule extends Component {
  constructor(props) {
    super(props)
    this.state = { rules: defaultRule }
    this.handleClose = this.handleClose.bind(this)
  }

  handleClose() {
    this.props.onHandleClose(false)
  }

  render() {
    return (
      <div className="dialog-rule">
        <div className="dialog-mask fixed"></div>
        <div className="rule-box bg">
          <span onClick={this.handleClose} className="rule-close bg"></span>
          <div className="rule-content">
            <h2 className="rule-title"></h2>
            <ul>
              {this.state.rules.map((val, index) => {
                return (
                  <li key={val.id} className="rule-item clearfix">
                    <span className="float-l">{index + 1}</span>
                    <div dangerouslySetInnerHTML={{ __html: val.txt }}></div>
                  </li>
                )
              })}
            </ul>
            <div className="jxwy-qrcode">
              <img className="qrcode-img" src={require('../../../assets/images/02/qrcode-wx.jpg')} alt="jxwy-qrcode"/>
              <p className="qrcode-tips"><br /></p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default DialogRule



